@charset "UTF-8";
/*Endast importer av partials*/
* {
  margin: 0;
  padding: 0;
}

header {
  border-bottom: 2.5px solid #ddd;
  display: flex;
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  padding: 1.5em 0 1em 0;
}
header a {
  font-size: 1em;
  letter-spacing: 0.5px;
  color: #333;
  text-decoration: none;
  display: inline-block;
  position: relative;
  color: #333;
  text-decoration: none;
  padding-bottom: 0.2em;
  font-family: "open sans";
  padding: 0.2em 0.4em;
}
header a:hover {
  color: #333;
}
header a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #999;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}
header a:hover::before {
  transform: scaleX(1);
}

.header_left {
  width: 25%;
  box-sizing: border-box;
  padding: 0 0.3em;
}
.header_left p {
  font-size: 1em;
  letter-spacing: 0.5px;
  color: #333;
  text-decoration: none;
  display: inline-block;
  position: relative;
  color: #333;
  text-decoration: none;
  padding-bottom: 0.2em;
  font-family: "open sans";
}

.header_right {
  width: 75%;
}
.header_right ul {
  list-style: none;
  text-align: right;
  margin-right: 0.3em;
}
.header_right ul li {
  display: inline-block;
  padding: 0 1em;
  margin: 0 0.3em;
}
.header_right ul li #active {
  background-color: #333;
  border-radius: 0.2em;
  color: white;
}

@media only screen and (max-width: 800px) {
  .header_left {
    width: 75%;
  }

  .header_right {
    width: 25%;
  }
  .header_right .menuBtn {
    width: 75px;
    height: 50px;
    background-color: white;
    border: none;
    border-radius: 0.3em;
    cursor: pointer;
    position: absolute;
    top: 0.8em;
    right: 3em;
  }
  .header_right .menuBtn img {
    text-align: right;
    width: 100%;
    height: 100%;
  }
  .header_right .menuBtn:hover {
    transform: scale(0.9);
  }
  .header_right ul {
    list-style: none;
    text-align: right;
    margin-right: 0.3em;
    background-color: white;
    width: 100%;
    height: 100vh !important;
    position: absolute;
    left: 0;
    right: 0;
    top: 7em;
    z-index: 999;
    transition: 1s;
  }
  .header_right ul li {
    display: block;
    text-align: center;
    padding: 0;
    margin: 2em 0;
  }
  .header_right ul li #active {
    background-color: #333;
    border-radius: 0.2em;
    color: white;
  }
}
.frame {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 1em;
  margin-bottom: 5em;
}

body {
  min-height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
}

main h1 {
  margin: 1em 0 0.25em 0;
  font-size: 4em;
  font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
}
main h2 {
  margin: 1em 0 0.25em 0;
  font-size: 2em;
  font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
  margin-top: 2em;
}
main p {
  font-size: 1em;
  font-family: "Open Sans", sans-serif;
  color: #333;
  margin: 1em 0;
  max-width: 65ch;
  line-height: 2em;
}
main p a {
  text-decoration: none;
  border-bottom: 1px solid;
}
main img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.3em;
}

.list404 {
  margin-left: 1.2em;
}
.list404 li {
  font-size: 1em;
  font-family: "Open Sans", sans-serif;
  color: #333;
  margin: 1em 0;
  max-width: 65ch;
  line-height: 2em;
}
.list404 li a {
  text-decoration: none;
  display: inline-block;
}
.list404 li a:hover {
  transform: scale(1.1);
}

@media all and (max-width: 750px) {
  .frame {
    max-width: 1200px;
    width: 90%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 1em;
    margin-bottom: 5em;
  }

  main h1 {
    margin: 1em 0 0.25em 0;
    font-size: 3.5em;
    font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
  }
  main h2 {
    margin: 1em 0 0.25em 0;
    font-size: 1.5em;
    font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
    margin-top: 2em;
  }
}
footer {
  margin-top: auto;
  background-color: #333;
  color: #ddd;
  text-align: center;
}
footer div {
  padding: 0.2em;
  max-width: 1200px;
  box-sizing: border-box;
  width: 90%;
  margin: 0 auto;
}
footer p {
  color: #ddd;
  font-size: 1em;
  font-family: "Open Sans", sans-serif;
  line-height: 2em;
}
footer p a {
  color: #ddd;
}
footer .top {
  text-align: center;
}
footer .top img {
  max-width: 75px;
  width: 100%;
  height: auto;
  border-radius: 50%;
  margin-top: 0.5em;
  padding: 0.3em;
}
footer .top img:hover {
  transform: scale(1.2);
  transition: 0.3s;
}

@media all and (max-width: 600px) {
  footer {
    display: block;
    position: relative;
  }
  footer .left, footer .right {
    text-align: left;
    height: 100px;
    vertical-align: middle;
  }
  footer .left {
    position: relative;
  }
  footer .left p {
    position: absolute;
    bottom: 0.2em;
  }
  footer .right {
    position: relative;
  }
  footer .right p {
    position: absolute;
    top: 0.2em;
  }
  footer .center {
    position: absolute;
    top: 0;
    right: 15%;
    width: 200px;
    height: 200px;
    padding-top: 50px;
    /* Half the height */
    text-align: center;
  }
  footer .center img {
    max-width: 125px;
  }
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #ddd;
}

::-webkit-scrollbar-thumb {
  background: #999;
}

::-webkit-scrollbar-thumb:hover {
  background: #666666;
}

table {
  width: 100%;
  max-width: 800px;
  border-spacing: 0;
  background-color: white;
}
table th {
  text-align: left;
  font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
  font-weight: bold;
  font-size: 1.2em;
  box-sizing: border-box;
  padding: 1em;
  border-bottom: 2px solid #ddd;
  background-color: white;
}
table td {
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  padding: 1.5em 1.5em 1.5em 1em;
  border-bottom: 2px solid #ddd;
  margin: 0 0.5em;
}
table tr:hover {
  background-color: #ddd;
}

@media all and (min-width: 760px) {
  th:nth-child(3),
th:nth-child(4) {
    width: 100px;
    text-align: center;
  }

  td:nth-child(3),
td:nth-child(4) {
    width: 100px;
    text-align: center;
  }
}
@media all and (max-width: 750px) {
  /* Force table to not be like tables anymore */
  table, thead, tbody, th, td, tr {
    display: block;
  }

  table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  table tr {
    border: 1px solid #ddd;
    margin-bottom: 1em;
  }
  table td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 40%;
    font-size: 0.95em;
  }
  table td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding: 1em;
    padding-right: 10px;
    white-space: nowrap;
  }
  table td:nth-of-type(1):before {
    content: "Kursnamn:";
    width: 100px;
  }
  table td:nth-of-type(2):before {
    content: "Kommentar:";
    width: 100px;
  }
  table td:nth-of-type(3):before {
    content: "Datum:";
    width: 100px;
  }
  table td:nth-of-type(4):before {
    content: "Status:";
    width: 100px;
  }
}
@media all and (max-width: 450px) {
  table td {
    padding-left: 50%;
  }

  fieldset {
    padding: 2em 1em;
  }

  form input[type=submit], .deleteBtn {
    width: 44%;
  }
}
.floatRigtht {
  float: right;
}

.button, .submitBtn, .deleteBtn, .addBtn {
  width: 150px;
  padding: 0.75em 1em;
  border: none;
  color: white;
  border-radius: 0.3em;
  cursor: pointer;
  background-color: #333;
  font-weight: bold;
  font-family: "Open Sans", sans-serif;
  margin-top: 1em;
}
.button:hover, .submitBtn:hover, .deleteBtn:hover, .addBtn:hover {
  transform: scale(0.9);
}

.addBtn {
  background-color: #78f17e;
  color: black;
  float: right;
}

.deleteBtn {
  background-color: #b3b3b3;
  color: black;
}

.submitBtn {
  float: right;
}

form {
  width: 100%;
}
form fieldset {
  border: 2px solid #ddd;
  border-radius: 0.2em;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  width: 100%;
  padding: 2em 3em;
}
form legend {
  font-size: 1.2em;
  font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
  font-weight: bold;
  padding: 0 0.2em;
}
form label {
  font-size: 1.1em;
  font-family: "Poiret One", Georgia, "Times New Roman", Times, serif;
  font-weight: bold;
}
form input {
  box-sizing: border-box;
  width: 100%;
  padding: 0.5em 0.3em;
  font-size: 0.9em;
  margin-bottom: 3em;
  margin-top: 0.5em;
  background-color: #ddd;
  border: none;
  border-radius: 0.3em;
  font-family: "Open Sans", sans-serif;
}
form textarea {
  width: 100%;
  height: 150px;
  resize: none;
  box-sizing: border-box;
  padding: 0.5em;
  font-family: "Open Sans", sans-serif;
  background-color: #ddd;
  border: none;
  border-radius: 0.3em;
  margin-top: 0.3em;
}
form ::placeholder {
  color: #999;
}
form .inputLocked {
  background-color: #999;
  border: 1px solid #333;
  color: #333;
  border-radius: 0.2em;
  cursor: not-allowed;
}
form #webbpageLinkLink {
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  margin-top: -6em !important;
  color: #333;
}

.loginForm {
  max-width: 500px;
  margin-top: 3em;
}
.loginForm label {
  color: white;
}
.loginForm button {
  background-color: #ddd;
  color: #333;
  border: 0.3em;
}

.adminForm {
  max-width: 600px;
}

.textFrom_imgDiv {
  max-width: 150px;
  margin-bottom: 2em;
  box-sizing: border-box;
}
.textFrom_imgDiv:hover {
